////MOBILE



.create-modal {
    position: fixed;
    // padding-top: 50px;
    width: 100%;
    height: 100%;
    z-index: 15;
    background-color: rgba(0, 0, 0, 0.645);

    .picker-container{
        position: absolute;
        left: 35%;
        top: 35%;
        z-index: 20;
    }
}

.create-page {
    height: 100vh;
    background-color: #595959;
    padding-top: 60px;
}

.create-post {
    background-color: #fff;
    z-index: 10;
    margin: auto;
    width: 918px;
    height: 621px;
    border-radius: 10px;


    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 42px;
        padding: 0 5px;
        border-bottom: 1px solid #dbdbdb;

        a {
            padding: 8px;
            cursor: pointer;

            i {
                font-size: 26px;
                color: #2a2a2a;
            }
        }

        span {
            font-size: 16px;
            font-weight: 600;
            line-height: 24px;
        }

        .share-btn {
            font-size: 14px;
            font-weight: 600;
            color: #0095f6;

            &:hover {
                color: #00376b;
            }
        }
    }
}

.upload-preview {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.create-post-container {
    display: grid;
    position: relative;
    grid-template-columns: 65% 35%;
    height: calc(100% - 42px);

    .img-section {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            // width: 100%;
            max-width: 100%;
            // max-height: 300px;
            max-height: 574px;
        }
    }

    .upload-preview {
        margin: auto;
    }

}

.post-info {
    border-left: 1px solid #dbdbdb;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .post-user-info {
        display: flex;
        flex-direction: column;
        gap: 15px;
        padding: 20px 16px;

    }

    section {
        display: flex;
        gap: 15px;
        align-items: center;

        img {
            width: 28px;
            height: 28px;
            border-radius: 50%;
            object-fit: cover;
        }

        span {
            font-size: 14px;
            font-weight: 600;
        }
    }

    form {
        height: 174px;

    }

    textarea {
        border: none;
        font-size: 16px;
        width: 95%;
        height: 200px;
        resize: none;

        &:focus {
            outline: none;

        }
    }

    .post-info-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px;

        a {
            color: #595959;
            font-size: 20px;
        }

        span {
            font-size: 12px;
            color: #595959;
        }
    }
}

@include for-mobile-layout {

    .create-post-container .img-section img {
        max-height: 60vh
    }

    .create-post-container .img-section {
        max-height: 100%;
        // object-fit: contain;
        object-fit: cover;
    }

    .post-info {
        border-top: 1px solid #dbdbdb;

        form {
            height: 100px;
        }
    }

    // .create-modal {
    //     position: absolute;
    //     // padding-top: 50px;
    //     width: 100%;
    //     height: 100vh;
    //     z-index: 15;
    //     background-color: rgba(0, 0, 0, 0.645);
    // }

    .create-post {
        width: 100%;
        height: 100vh;
        border-radius: 0;
    }

    .create-post-container {
        height: 100%;
        grid-template-columns: none;
        grid-template-rows: calc(65% - 21px) calc(35% - 21px);
    }
}


///////// normal //////
